@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  height: 100vh;
  width: 100%;
  background-color: purple;
  justify-content: center;
  align-items: center;
  display: flex;
}

button {
  color: #fff;
  letter-spacing: 2px;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 50px;
  font-size: 18px;
  font-weight: 600;
}

.submitBtn {
  width: 180px;
  height: 50px;
}

.submitBtn:active {
  transform: scale(.95);
}

.pop-up {
  width: 400px;
  visibility: hidden;
  background-color: #fff;
  border-radius: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: all .5s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%)scale(.01);
}

.pop-up-open {
  visibility: visible;
  top: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.pop-up img {
  width: 100px;
  position: absolute;
  top: -50px;
  left: 38%;
}

.pop-up h2 {
  color: red;
  margin-top: 30px;
}

.pop-up p {
  text-align: center;
  margin: 15px 0;
  font-size: 15px;
}

.pop-up .okBtn {
  width: 80px;
  height: 40px;
}

